import React, { useContext } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import logo from '../logo.svg'
import logo2 from '../logo2.svg'
import context from '../stores/index'
import { observer } from 'mobx-react'
import { HeaderWrapper, Logo, HeaderNavLink, StyledButton } from './Styled'

const Component = observer((props) => {
  const { AuthStore, UserStore } = useContext(context)
  let navigate = useNavigate()
  const HandleLogout = () => {
    AuthStore.logout()
  }
  const HandleLogin = () => {
    navigate('/login')
  }
  const HandleRegister = () => {
    navigate('/register')
  }
  React.useEffect(() => {
    UserStore.getCurrentUser()
  })
  return (
    <HeaderWrapper>
      <nav>
        <Logo
          src={logo}
          alt="logo"
          onClick={() => (window.location.href = '/')}
        />
        <HeaderNavLink to="/"></HeaderNavLink>
        <HeaderNavLink to="history"></HeaderNavLink>
        <HeaderNavLink to="about"></HeaderNavLink>
      </nav>
      <div>
        {UserStore.currentUser ? (
          <>
            {UserStore.currentUser.attributes.username}
            <StyledButton type="dashed" onClick={HandleLogout}>
              <Link to="/"></Link>
            </StyledButton>
          </>
        ) : (
          <>
            <StyledButton type="dashed" onClick={HandleLogin}>
              <Link to="login"></Link>
            </StyledButton>
            <StyledButton type="dashed" onClick={HandleRegister}>
              <Link to="register"></Link>
            </StyledButton>
          </>
        )}
        <Logo src={logo2} onClick={() => props.toggle()} />
      </div>
    </HeaderWrapper>
  )
})

export default Component
